0px;\n  left: 0;\n  background-color: var(--primary);\n  color: var(--white);\n  padding: 8px;\n  z-index: 100;\n}\n\n.skip-link:focus {\n  top: 0;\n}\n\n/* \n * header\n */\n.hero {\n  display: flex;\n  flex-direction: column;\n  min-height: 580px;\n  width: 100%;\n  height: 100%;\n  text-align: center;\n  background: linear-gradient(to top, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.6) 60%, rgba(0, 0, 0, 0.8) 100%), url(" + ___CSS_LOADER_URL_REPLACEMENT_0___ + ");\n  background-position: center;\n  object-fit: cover;\n  padding: 0 10%;\n}\n\n.hero-text {\n  align-self: center;\n  max-width: 600px;\n  margin: auto 0;\n  padding-bottom: 3em;\n}\n\n.hero-title {\n  color: var(--white);\n  font-weight: 600;\n  font-size: xx-large;\n}\n\n.hero-subtitle {\n  color: var(--white);\n  margin: 16px;\n  margin-bottom: 30px;\n  font-size: 20px;\n  font-weight: 500;\n  word-spacing: 3px;\n  line-height: 1.4em;\n}\n\n/* Button */\n.btn {\n  margin: 2em;\n  padding: 0.5em 1em;\n  font-weight: 700;\n  color: var(--white);\n  text-align: center;\n  vertical-align: middle;\n  background-color: var(--primary);\n  border: 5px solid transparent;\n  font-size: large;\n  cursor: pointer;\n  text-decoration: none;\n  border-radius: 10px;\n}\n\n.btn:hover {\n  background-color: var(--hover);\n  color: var(--secondary);\n  transition: 0.4s;\n}\n\n/*\n * Main Content\n */\n.explore-restaurant{\n  width: 100%;\n  margin: 0px auto;\n  text-align: center;\n}\n\n.explore-label{\n  font-size: 28px;\n  margin-top: 44px;\n  padding-top: 12px;\n  margin-bottom: 0px;\n}\n\nmain {\n  flex: 1;\n}\n\n.content {\n  margin: 0 auto;\n  min-height: 100%;\n}\n\n/*\n * restaurant card item\n */\n.restaurants {\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 16px;\n  margin: 32px 0;\n  padding: 32px;\n}\n\n.restaurant-item {\n  width: 100%;\n  background-color: var(--hover);\n  border-radius: 10px;\n  overflow: hidden;\n}\n\n.restaurant-item:hover {\n  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.4);\n  transition: 0.4s;\n}\n\n.restaurant-item-header {\n  position: relative;\n}\n\n.restaurant-item .restaurant-item-header .restaurant-item-poster {\n  width: 100%;\n  height: 240px;\n  object-fit: cover;\n  object-position: center;\n}\n\n.restaurant-item .restaurant-item-header .restaurant-item-rating {\n  position: absolute;\n  top: 0;\n  left: 0;\n  background-color: var(--primary);\n  color: var(--white);\n  padding: 0.5em 1em;\n  z-index: 1;\n  border-radius: 0px 0px 10px 0px;\n}\n\n.restaurant-item .restaurant-item-header .restaurant-item-rating .fa{\n  padding: 0.3em 0.8em 0.3em 0;\n  color: yellow;\n}\n\n.restaurant-item .restaurant-item-header .restaurant-item-rating .restaurant-iteard {\n  margin-left: 10px;\n}\n\n.restaurant-item .restaurant-item-card {\n  padding: 16px;\n}\n\n.restaurant-item .restaurant-item-card h2 {\n  margin: 0 0 10px 0;\n}\n\n.restaurant-item .restaurant-item-card h2 a {\n  color: var(--primary);\n  text-decoration: none;\n  padding: 10px;\n  display: block;\n  text-align: center;\n}\n\n.restaurant-item .restaurant-item-card h2 a:hover {\n  color: var(--secondary);\n  transition: 0.4s;\n}\n\n.restaurant-item .restaurant-item-card p {\n  color: var(--font-color);\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 4;\n  -webkit-box-orient: vertical;\n}\n\n/*\n * restaurant detail\n */\n.restaurant {\n  padding: 32px;\n  display: flex;\n  flex-direction: column;\n  align-items: start;\n}\n\n.restaurant-title {\n  padding-bottom: 20px;\n  font-size: 28px;\n  color: var(--primary);\n}\n\n.restaurant-poster {\n  width: 100%;\n  max-width: 800px;\n  height: 400px;\n  object-fit: cover;\n  object-position: center;\n}\n\n.restaurant-information {\n  padding-top: 36px;\n  padding-bottom: 24px;\n}\n\n.restaurant-info .detail-info .icon-primary {\n  color: var(--primary);\n}\n\n.detail-info {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-evenly;\n  padding-left: 0;\n  margin-bottom