 {\n  color: var(--primary);\n  transition: 0.4s;\n}\n\n.navbar .navbar-nav {\n  position: absolute;\n  top: 50px;\n  left: -180px;\n  width: 150px;\n  transition: all 0.4s;\n  padding: 8px;\n  background-color: var(--secondary);\n  overflow: hidden;\n}\n\n.navbar .navbar-nav.open {\n  left: 0;\n}\n\n.navbar .navbar-nav ul li a {\n  display: inline-block;\n  text-decoration: none;\n  color: var(--white);\n  font-size: 18px;\n  padding: 20px;\n  margin-bottom: 5px;\n  width: 100%;\n}\n\n.navbar .navbar-nav ul li a:hover{\n  color: var(--primary);\n  transition: 0.4s;\n}\n\n/* Skip link */\n.skip-link {\n  position: absolute;\n  top: -50px;\n  left: 0;\n  background-color: var(--primary);\n  color: var(--white);\n  padding: 8px;\n  z-index: 100;\n}\n\n.skip-link:focus {\n  top: 0;\n}\n\n/* \n * header\n */\n.hero {\n  display: flex;\n  flex-direction: column;\n  min-height: 580px;\n  width: 100%;\n  height: 100%;\n  text-align: center;\n  background: linear-gradient(to top, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.6) 60%, rgba(0, 0, 0, 0.8) 100%), url(\"../public/images/heros/hero-image_4.jpg\");\n  background-position: center;\n  object-fit: cover;\n  padding: 0 10%;\n}\n\n.hero-text {\n  align-self: center;\n  max-width: 600px;\n  margin: auto 0;\n  padding-bottom: 3em;\n}\n\n.hero-title {\n  color: var(--white);\n  font-weight: 600;\n  font-size: xx-large;\n}\n\n.hero-subtitle {\n  color: var(--white);\n  margin: 16px;\n  margin-bottom: 30px;\n  font-size: 20px;\n  font-weight: 500;\n  word-spacing: 3px;\n  line-height: 1.4em;\n}\n\n/* Button */\n.btn {\n  margin: 2em;\n  padding: 0.5em 1em;\n  font-weight: 700;\n  color: var(--white);\n  text-align: center;\n  vertical-align: middle;\n  background-color: var(--primary);\n  border: 5px solid transparent;\n  font-size: large;\n  cursor: pointer;\n  text-decoration: none;\n  border-radius: 10px;\n}\n\n.btn:hover {\n  background-color: var(--hover);\n  color: var(--secondary);\n  transition: 0.4s;\n}\n\n/*\n * Main Content\n */\n.explore-restaurant{\n  width: 100%;\n  margin: 0px auto;\n  text-align: center;\n}\n\n.explore-label{\n  font-size: 28px;\n  margin-top: 44px;\n  padding-top: 12px;\n  margin-bottom: 0px;\n}\n\nmain {\n  flex: 1;\n}\n\n.content {\n  margin: 0 auto;\n  min-height: 100%;\n}\n\n/*\n * restaurant card item\n */\n.restaurants {\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 16px;\n  margin: 32px 0;\n  padding: 32px;\n}\n\n.restaurant-item {\n  width: 100%;\n  background-color: var(--hover);\n  border-radius: 10px;\n  overflow: hidden;\n}\n\n.restaurant-item:hover {\n  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.4);\n  transition: 0.4s;\n}\n\n.restaurant-item-header {\n  position: relative;\n}\n\n.restaurant-item .restaurant-item-header .restaurant-item-poster {\n  width: 100%;\n  height: 240px;\n  object-fit: cover;\n  object-position: center;\n}\n\n.restaurant-item .restaurant-item-header .restaurant-item-rating {\n  position: absolute;\n  top: 0;\n  left: 0;\n  background-color: var(--primary);\n  color: var(--white);\n  padding: 0.5em 1em;\n  z-index: 1;\n  border-radius: 0px 0px 10px 0px;\n}\n\n.restaurant-item .restaurant-item-header .restaurant-item-rating .fa{\n  padding: 0.3em 0.8em 0.3em 0;\n  color: yellow;\n}\n\n.restaurant-item .restaurant-item-header .restaurant-item-rating .restaurant-iteard {\n  margin-left: 10px;\n}\n\n.restaurant-item .restaurant-item-card {\n  padding: 16px;\n}\n\n.restaurant-item .restaurant-item-card h2 {\n  margin: 0 0 10px 0;\n}\n\n.restaurant-item .restaurant-item-card h2 a {\n  color: var(--primary);\n  text-decoration: none;\n  padding: 10px;\n  display: block;\n  text-align: center;\n}\n\n.restaurant-item .restaurant-item-card h2 a:hover {\n  color: var(--secondary);\n  transition: 0.4s;\n}\n\n.restaurant-item .restaurant-item-card p {\n  color: var(--font-color);\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 4;\n  -webkit-box-orient: vertical;\n}\n\n/*\n * restaurant detail\n */\n.restaurant {\n  padding: 32px;\n  display: flex;\n  flex-direction: column;\n  align-items: start;\n}\n\n.restaurant-title {\n  padding-bottom: 20px;\n  font-size: 28px;\n  color: var(--primary);\n}\n\n.restaurant-poster {\n  width: 100%;\n  max-width: 800px;\n  height: 400px;\n  object-fit: cover;\n  object-position: center;\n}\n\n.restaurant-information {\n  padding-top: 36px;\n  padding-bottom: 24px;\n}\n\n.restaurant-info .detail-info .icon-primary {\n  color: var(--primary);\n}\n\n.detail-info {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-evenly;\n  padding-left: 0;\n  margin-bottom: 0;\n  gap: 0px;\n  max-width: 800px;\n}\n\n.detail-info li {\n  position: relative;\n  display: block;\n  padding: 0.6em;\n  color: var(--font-color);\n  text-decoration: none;\n  background-color: var(--primary-color);\n}\n\n.detail-desc {\n  text-align: justify;\n  color: #8f8f8f;\n  margin-bottom: 0.5em;\n  font-style: italic;\n}\n\n.detail-name-address-rating {\n  display: inline-block;\n}\n\n.restaurant-info .info-detail{\n  display: flex;\n  flex-direction: column;\n  gap: 0px;\n  max-width: 800px;\n}\n\n.restaurant-info .info-detail h2 {\n  margin-top: 20px;\n}\n\n.restaurant-info .info-detail .detail-item {\n  padding: 0.5em 1.5em;\n  color: var(--white);\n  background-color: var(--primary);\n  margin-right: 5px;\n  margin-bottom: 5px;\n  border-radius: 0px 10px 0px 10px;\n  border: 2px solid var(--primary);\n  cursor: pointer;\n  display: inline-block;\n}\n\n.restaurant-info .info-detail .detail-item:hover {\n  background-color: var(--white);\n  color: var(--primary);\n  border: 2px solid var(--primary);\n  transition: 0.4s;\n}\n\n/* review */\n.detail-review {\n  max-width: 100%;\n  font-size: 1em;\n  text-align: center;\n}\n\n.detail-review-item {\n  color: var(--font-color);\n  background-color: var(--hover);\n  margin-bottom: 1.5em;\n  padding: 1em 1em;\n  border: 1px solid var(--secondary);\n  border-radius: 0.8em;\n}\n\n.review-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 1em 1em;\n  color: var(--secondary);\n  border-bottom: 1px solid var(--font-color);\n}\n\n.review-name {\n  font-weight: bold;\n  display: flex;\n  align-items: center;\n}\n\n.review-date {\n  font-size: 0.8em;\n  font-weight: lighter;\n}\n\n.review-body {\n  padding: 1em;\n  text-align: left;\n}\n\n/*\n * favorite\n */\n.like {\n  font-size: 18px;\n  background-color: var(--primary);\n  color: var(--white);\n  border: 0;\n  border-radius: 50%;\n  width: 55px;\n  height: 55px;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  z-index: 10;\n}\n\n.content-heading {\n  text-align: center;\n  padding: 32px;\n  margin-bottom: -70px;\n}\n\n#empty-content{\n  text-align: center ;\n}\n\n/*\n * footer\n */\nfooter {\n  padding: 2em;\n  width: 100%;\n  text-align: center;\n  border-top: 3px solid var(--secondary);\n  background-color: var(--hover);\n}\n\nfooter ul {\n  margin: 0 auto;\n  display: flex;\n  flex-direction: column;\n  list-style: none;\n}\n\nfooter li {\n  margin: 0.3em 0;\n  font-size: 14px;\n}\n"],"sourceRoot":""}]);
